import { NgClass, NgIf } from '@angular/common';
import {
  Component,
  ElementRef,
  Input,
  OnInit,
  Optional,
  Renderer2,
  Self,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { FormGroupDirective, FormsModule, NgControl, NgForm, ReactiveFormsModule, ValidatorFn } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS, MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatButtonModule } from '@angular/material/button';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { A11yModule } from '@angular/cdk/a11y';

import {
  acceptValidator,
  BaseComponent,
  formatDataStorageUnits,
  isNotEmptyArray,
  maxTotalFileSizeValidator
} from 'sidsc-components/core';
import { DscButtonComponent } from 'sidsc-components/dsc-button';
import { DscTooltipModule } from 'sidsc-components/dsc-tooltip';


@Component({
  selector: 'dsc-input-file',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './dsc-input-file.component.html',
  styleUrls: ['./dsc-input-file.component.scss'],
  imports: [
    NgIf,
    NgClass,
    FormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatIconModule,
    MatTooltipModule,
    DscTooltipModule,
    MatButtonModule,
    ReactiveFormsModule,
    DscButtonComponent,
    A11yModule
  ],
  providers: [
    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: { appearance: 'outline' } }
  ]
})
export class DscInputFileComponent extends BaseComponent<any> implements OnInit {
  @ViewChild('inputFile', { static: true, read: ElementRef })
  inputFile!: ElementRef;

  @Input()
  get showTotalFileSize(): boolean {
    return this._showTotalFileSize;
  }

  set showTotalFileSize(value: boolean | string) {
    this._showTotalFileSize = coerceBooleanProperty(value);
  }

  private _showTotalFileSize = false;

  isActive: boolean = false;

  @Input()
  get multiple(): boolean {
    return this._multiple;
  }

  set multiple(value: boolean | string) {
    this._multiple = coerceBooleanProperty(value);
  }

  private _multiple = false;

  @Input()
  get maxTotalSize(): number {
    return this._maxTotalSize;
  }
  set maxTotalSize(value: number | string) {
    const length = typeof value === 'string' ? parseInt(value) : value;
    if (!isNaN(length) && Number.isInteger(length)) this._maxTotalSize = length;
  }

  private _maxTotalSize: number = NaN;

  @Input()
  accept?: string;

  totalFileSize = '0 bytes';

  fileName?: string;
  constructor(@Optional() @Self() public ngControl: NgControl,
              @Optional() parentForm: NgForm,
              @Optional() parentFormGroup: FormGroupDirective,
              private _renderer: Renderer2
  ) {
    super(ngControl, parentForm, parentFormGroup);
    if (this.ngControl) this.ngControl.valueAccessor = this;
  }

  ngOnInit(): void {
    this.updateOn = this.ngControl?.control?.updateOn;
    this._setMultipleAttribute();
    this._setAcceptAttribute();
    this._initValidators();
  }

  private _setMultipleAttribute() {
    this._multiple
      ? this._renderer.setAttribute(this.inputFile.nativeElement, 'multiple', '')
      : this._renderer.removeAttribute(this.inputFile.nativeElement, 'multiple');
  }

  private _setAcceptAttribute(): void {
    if (this.accept) {
      const values = this.accept.replace(/\s/g, '').split(',');
      if (isNotEmptyArray(values)) {
        this._renderer.setAttribute(this.inputFile.nativeElement, 'accept', values.join());
      } else {
        this._removeAcceptAttribute();
      }
    } else {
      this._removeAcceptAttribute();
    }
  }

  private _removeAcceptAttribute(): void {
    this._renderer.removeAttribute(this.inputFile.nativeElement, 'accept');
  }

  private _initValidators(): void {
    if (this.ngControl) {
      const control = this.ngControl.control;
      control?.addValidators(this._getValidators());
      control?.updateValueAndValidity();
    }
  }

  private _getValidators(): ValidatorFn[] {
    return [acceptValidator(this.accept), maxTotalFileSizeValidator(this._maxTotalSize)];
  }

  override writeValue(value: any): void {
    if (!value) {
      this.fileName = undefined;
      this.totalFileSize = '0 bytes';
    }
    this.val = value;
  }

  onClick($event: Event): void {
    $event.stopPropagation();
    if (!this.disabled) this.inputFile.nativeElement.click();
  }

  public onInputChange($event: Event): void {
    const input = $event.target as HTMLInputElement;
    let value: any;
    let numberOfBytes = 0;
    this.showTotalFileSize = '0 bytes';
    this.fileName = '';

    if (input && input.files && input.files.length > 0) {
      if (this.multiple) {
        const files: File[] = (value = Array.from(input.files));
        this.fileName = files.map(({ name }) => name).join(', ');
        numberOfBytes = files.map(({ size }) => size).reduce((sum, current) => sum + current, 0);
      } else {
        const file: File = (value = input.files[0]);
        this.fileName = file.name;
        numberOfBytes = file.size;
      }

      this.totalFileSize = formatDataStorageUnits(numberOfBytes);
    }

    this.writeValue(value);
  }

  get formFieldClasses(): string[] {
    return [
      this.ngControl && this.ngControl.value?.length
        ? 'mat-mdc-form-field--filled'
        : 'mat-mdc-form-field--empty',
      this.readOnly ? 'mat-mdc-form-field--readonly' : '',
      this.ngControl &&
      this.ngControl.invalid &&
      (this.ngControl.touched || this.ngControl.dirty) &&
      !this.readOnly
        ? 'error-field'
        : '',
        this.isActive || (this.ngControl && this.ngControl.value?.length)
      ? 'active-field'
      : ''
    ].filter(Boolean);
  }
}
